<template>
  <div id="app">
     <div class="upload-form">
        <h1>Upload Folder</h1>
        <ul>
           <li class="folder" :class="{selected: folder.folderId === folderId}" v-on:click="folderId = folder.folderId" v-for="folder in folders">{{folder.name}}</li>
        </ul>
        <form enctype="multipart/form-data" class="uploadForm">
            <input name="file" type="file" v-on:change="setFile()"/>
            <input type="hidden" name="folderId" v-model="folderId">
            <p class="error" v-if="folderId === -1">No folder selected</p>
            <p class="error" v-if="uploadFile.length === 0">No file selected</p>
            <input v-if="validate()" type="button" class="upload-excel" value="Upload" v-on:click="upload"/><p v-if="msg.length > 0">{{msg}}</p>
        </form>
        <div class="progressbar-container"><div>{{progress === 0 ? 'Progress' : (progress + '%')}}</div></div>
     </div>
     <div class="file-info">
        <h1>File Info</h1>
        <table>
           <tr><th>File Entry Id:</th><td>{{fileEntry.fileEntryId}}</td></tr>
           <tr><th>Title:</th><td>{{fileEntry.title}}</td></tr>
           <tr><th>Extension:</th><td>{{fileEntry.extension}}</td></tr>
           <tr><th>Version:</th><td>{{fileEntry.version}}</td></tr>
           <tr><th>Company Id:</th><td>{{fileEntry.companyId}}</td></tr>
           <tr><th>Craete date:</th><td>{{new Date(fileEntry.creatDate)}}</td></tr>
           <tr><th>Description:</th><td>{{fileEntry.description}}</td></tr>
           <tr><th>Mime Type:</th><td>{{fileEntry.mimeType}}</td></tr>
        </table>
     </div>
     <div class="liferay-properties">
        <h1>Portal Properties</h1>
        <button v-on:click="getProperties">Get Propertiess</button><br />
        <table>
           <thead>
              <tr>
                 <th>Key</th>
                 <th>Value</th>
              </tr>
              <tr>
                <td><input type="text" v-model="keyfilter" placeholder="..."></td>
                <td><input type="text" v-model="valuefilter" placeholder="..."></td>
              </tr>
           </thead>
           <tbody>
              <tr v-for="prop in filter(properties)">
                <td>{{prop.key}}</td>
                <td>{{prop.value}}</td>
              </tr>
           </tbody>
        </table>
        <a v-on:click="previous">Letzte</a>  {{offset+1}} bis {{offset + 1 + pagesize}} von {{filtered}}  <a v-on:click="next">Nächste</a>
     </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      fileEntry: {},
      uploadFileURL: 'not set!',
      ajaxURL: 'not set!',
      folders: {},
      progress: 0,
      uploadFile: '',
      folderId: -1,
      properties: [],
      filtered: 0,
      propkey: '',
      propvalue: '',
      msg: '',
      keyfilter: '',
      valuefilter: '',
      offset: 0,
      pagesize: 10
    }
  },
  props: ['namespace'],
  methods: {
    next: function () {
      this.offset = this.offset + this.pagesize;
    },
    previous: function () {
      this.offset = this.offset - this.pagesize;
    },
    filter: function (items) {
       var key;
       var item;
       var results = [];
       for (key in items) {
         if (items.hasOwnProperty(key)) {
            item = items[key];
            if ((this.keyfilter.length === 0 || item.key.indexOf(this.keyfilter) !== -1) &&
              (this.valuefilter.length === 0 || item.value.indexOf(this.valuefilter) !== -1)) {
             results.push(item);
           }
         }
       }
       this.filtered = results.length;
       results = results.slice(Math.min(this.offset, results.length), Math.min(this.offset + this.pagesize, results.length));
       return results;
    },
    setFile: function () {
      var files = jQuery('.uploadForm input[name=file]')[0].files;
      if (files.length > 0) {
         this.uploadFile = files[0];
      }
    },
    validate: function () {
      if (jQuery('.uploadForm input[name=file]').length > 0) {
      	var fileslength = jQuery('.uploadForm input[name=file]')[0].files.length;
         var self = this;
         return (fileslength > 0 && this.folderId != -1);
      } else {
         return false;
      }
    },
    upload: function () {
      var formData = new FormData(jQuery('.uploadForm')[0]);
   	var fileslength = jQuery('.uploadForm input[name=file]')[0].files.length;
      var self = this;
      formData.append('action', 'upload');
   	if (fileslength === 0) {
   		self.msg = 'no file selected';
   		return;
   	}
      this.progress = 0;
   	jQuery.ajax({
   		url: this.uploadFileURL,
   		type: 'POST',
   		dataType : "json",
   		success : function (response) {
            self.fileEntry = response;
            self.msg = 'Upload success';
   		},
   		complete : function () {
   		},
   		error: function () {
   			self.msg = 'Upload failed';
   		},
         xhr: function () {
           var xhr = jQuery.ajaxSettings.xhr();
           xhr.upload.onprogress = function (evt) {
             self.progress = Math.floor(evt.loaded / evt.total*100);
             jQuery('.progressbar-container div').css('width', self.progress + '%');
           };
           return xhr;
         },
   		data: formData,
   		cache: false,
   		contentType: false,
   		processData: false
   	});
   },
   getProperties: function () {
     var self = this;
     jQuery.ajax({
        url: this.ajaxURL,
        type: 'POST',
        dataType : "json",
        success : function (response) {
           self.properties = response;
        },
        complete : function () {
           console.log('complete');
        },
        error: function () {
           self.msg = 'error';
        },
        data: {
           'action': 'get-properties'
        }
     });
  },
  setProperty: function () {
    jQuery.ajax({
       url: this.ajaxURL,
       type: 'POST',
       dataType : "json",
       success : function (response) {
       },
       complete : function () {
       },
       error: function () {
          self.msg = 'error';
       },
       data: {
          'action': 'set-property',
          'key': this.propkey,
          'value': this.propvalue
       }
    });
    }
  }
}
</script>

<style>

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
}
.progressbar-container {
   position: relative;
   width: 100%;
   height: 20px;
   border: 1px solid gray;
   text-align: center;
}
.progressbar-container div {
   position: absolute;
   top: 0px;
   left: 0px;
   height: 20px;
   background-color: lightblue;
   width: 0%;
}
.upload-form {
   vertical-align: top;
   display: block;
   margin: 5px;
   width: 300px;
}

.file-info {
   vertical-align: top;
   display: block;
   margin: 5px;
   width: 300px;
}

.liferay-properties {
   vertical-align: top;
   display: block;
   margin: 5px;
   width: 300px;
}

li.folder {
   cursor: pointer;
   background: transparent url('./assets/img/folder.png')  no-repeat scroll 0 0 / 16px 16px;
   list-style: outside none none;
   text-indent: 20px;
}
p.error {
   color: red;
}
tr td, tr th {
   line-height: 1.5em;
}
h1 {
   font-weight: bold;
   width: 100%;
   border-bottom: 1px solid black;
}
li.selected {
   font-weight: bold;
}
td {
   white-space: nowrap;
}
th {
   font-weight: bold !important;
}
ul {
  list-style-type: none;
  padding: 0;
}

a {
   cursor: pointer;
   color: #42b983;
}
</style>



